import React, {useState} from 'react'; 
import '../Style/nav.css';
import { BrowserRouter as Router, Route, Link, NavLink, BrowserRouter } from "react-router-dom";
import { RiArrowDropDownLine } from 'react-icons/ri';
import { AiOutlineMenu } from 'react-icons/ai';

const Navbar = () => {
    const [nav, setNav]=useState(true);
    const [mob, setMob]=useState(false);
    const magic=()=>{
        if(window.scrollY>=100){
            setNav(true)
        }
        else{
            setNav(false)
        }
    }
 
    window.addEventListener('scroll', magic);
    return (
        <div className={nav?'nav-black': 'nav'}>
            <div className="dropdown">
                <button className="dropbtn">ADVENTURES
                   <RiArrowDropDownLine/>
                </button>
                <div >
                    <AiOutlineMenu className='menu-icon' onClick={()=>setMob(!mob)} size='30px'/>
                    <a href='#' className={mob?'mobile-menu':'hide-menu'}>
                    <ul  onClick={()=>setMob(!mob)}  className='mob-ul'>
                        <li> <NavLink path to='/about' activeStyle={{ fontWeight: "bold",color: nav?'#rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}>ABOUT TAN </NavLink> </li>
                        <li> <NavLink  path to='/gallery' activeStyle={{ fontWeight: "bold",color: nav?'rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}> GALLERY</NavLink> </li>
                        <li> <NavLink path to='/tanventure/' activeStyle={{ fontWeight: "bold",color: nav?'rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}> TANVENTURE</NavLink> </li>
                        <li> <NavLink  path to='/blog' activeStyle={{ fontWeight: "bold",color: nav?'rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}> BLOG  </NavLink> </li>
                    </ul>
                </a>
                </div>


                <div class="dropdown-content">
                    <a href="#">BANGLADESH</a>
                    <a href="#">INDIA</a>
                    <a href="#">SRILANKA</a>
                 </div>

             </div> 

            <ul className='nav-ul'>
                 <li> <NavLink path to='/about' activeStyle={{ fontWeight: "bold",color: nav?'#rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}>ABOUT TAN </NavLink> </li>
                 <li> <NavLink  path to='/gallery' activeStyle={{ fontWeight: "bold",color: nav?'rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}> GALLERY</NavLink> </li>
                 <li> <NavLink path to='/tanventure/' activeStyle={{ fontWeight: "bold",color: nav?'rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}> TANVENTURE</NavLink> </li>
                 <li> <NavLink  path to='/blog' activeStyle={{ fontWeight: "bold",color: nav?'rgb(29, 99, 27)': 'rgb(29, 99, 27)', opacity:'.6'}}> BLOG  </NavLink> </li>
           </ul>
        </div>
    )
}

export default Navbar
